/* The switch - the box around the slider */

.switch {
  position: relative;
  display: inline-block;
  width: 120px;
  height: 50px;
}

/* Hide default HTML checkbox */

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: all 0.5s ease-out;
}

.slider:before {
  position: absolute;
  content: "";
  height: 40px;
  width: 40px;
  left: 5px;
  bottom: 5px;
  background-color: white;
  transition: all 0.5s ease-out;
}

input:checked + .slider {
  background-color: #52b788;
}

input:focus + .slider {
  box-shadow: 0 0 1px #52b788;
}

input:checked + .slider:before {
  -webkit-transform: translateX(70px);
  transform: translateX(70px);
}

/* Rounded sliders */

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

* {
  -webkit-tap-highlight-color: transparent;
}

.flipTable {
  direction: rtl;
}

